    color: #1977cc; /* Blue color for file names */
    text-decoration: none; /* No underline by default */
    margin-right: 10px;
}

.file-name:hover {
    text-decoration: underline; /* Underline on hover */
}

.btn-danger.delete-btn {
    color: #fff;
    background-color: #dc3545;
    transition: opacity 0.3s ease;
}

.btn-danger.delete-btn:hover {
    opacity: 0.7;
}

.upload-button {
    background-color: #28a745;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    font-size: 16px;
}

.upload-button:hover {
    background-color: #22be46e7;
}

.custom-file-input {
    display: flex;
    position: relative;
    width: 100%;
    margin-bottom: 30px;
  }
  
  .custom-file-input .form-control {
    width: calc(100% - 120px); /* Leave space for the button */
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    margin-right: -1px; /* Ensures the input and label are visually connected */
    border-radius: 5px 0 0 5px;
  }
  
  .custom-file-input .file-input-label {
    display: inline-block;
    background-color: #369ef3;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    width: 120px;
    text-align: center;
  }
  
  .custom-file-input .file-input-label input[type="file"] {
    display: none;
  }
  
  .custom-file-input .file-input-label:hover {
    background-color: #1977cc;
  }
  

  /*My Patients list*/

  .patient-cards-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* Two columns of equal width */
    gap: 20px; /* Space between the grid items */
}

.patient-card {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    transition: transform 0.3s;
    box-sizing: border-box; /* Ensure padding and border are included in the width */
}

.patient-card:hover {
    transform: translateY(-10px);
}

.patient-image {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 20px;
}

.patient-info {
    flex: 1;
}

.patient-name {
    font-size: 1.7rem;
    color: #007bff;
    margin-bottom: 0px;
}

.patient-details {
    font-size: 1rem;
    color: #343a40;
    margin-top: 10px;
    margin-bottom: 5px;
}

.patient-history {
    font-size: 1rem;
    color: #666;
    margin-bottom: 15px;
}

.patient-divider {
    width: 60px;
    margin: 5px 0;
    border: 0;
    border-top: 2px solid #007bff;
}

.patient-files {
    margin-top: 15px;
}

.btn-view-files {
    background-color: #dce3dd;
    color: #000000;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    font-size: 1rem;
}

.btn-view-files:hover {
    background-color: #deeee2;
}

.patients-section h2{
    padding : 50px 15px;
    margin: 25px 0px;
}
 
.mypatient-button {
    background-color: #1977cc;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    font-size: 16px;
    margin-top: 15px;
}
.mypatient-button:hover {
    background-color: #135b9b;
}
/* Footer */
.app-footer {
    background-color: #f8f9fa;
    color: #343a40;
    text-align: center;
    padding: 20px;
    bottom: 0;
    width: 100%;
}

.btn-chat-now {
    margin: 15px 0;
    padding: 5px 5px;
}

/* Chat Container */
.chat-container {
    position: fixed;
    bottom: 50px;
    right: 10px;
    width: 360px;
    height: 400px;
    background-color: white;
    border: 1px solid #ccc;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.chat-tab {
    position: fixed;
    bottom: 0;
    right: 0;
    padding: 15px 25px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 12px 12px 0 0;
    cursor: pointer;
    z-index: 1001;
    transition: background-color 0.3s ease;
    width: 30%;
    
}

.chat-tab:hover {
    background-color: #0056b3;
}

.chat-small {
    width: 360px;
    height: 400px;
}

.chat-large {
    width: 100%;
    height: 100vh;
}

.chat-sidebar {
    width: 100%;
    background-color: #f4f4f4;
    padding: 20px;
    border-right: 1px solid #ccc;
    overflow-y: auto;
    display: block;
}

.chat-sidebar ul {
    list-style: none;
    padding: 0;
}

.chat-sidebar li {
    padding: 10px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.chat-sidebar li.active,
.chat-sidebar li:hover {
    background-color: #e0e0e0;
}

.chat-window {
    width: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100%;
}

.chat-header {
    background-color: #f4f4f4;
    padding: 10px;
    border-bottom: 1px solid #ccc;
}

.back-arrow {
    background: none;
    border: none;
    font-size: 16px;
    cursor: pointer;
}

.chat-messages {
    flex-grow: 1;
    padding: 10px;
    overflow-y: auto;
    background-color: #fff;
    max-height: calc(100% - 60px); /* Adjust this if needed */
}

.chat-input {
    display: flex;
    padding: 10px;
    border-top: 1px solid #ccc;
    background-color: #f9f9f9;
}

.chat-input input {
    flex-grow: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-right: 10px;
}

.chat-input button {
    padding: 10px 20px;
    background-color: #1977cc;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

/* Message Box Styles */
.message {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.message.right {
    justify-content: flex-end;
}

.message.left {
    justify-content: flex-start;
}

.message-avatar {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin-right: 10px;
}

.message-box {
    margin: 10px 0;
    padding: 10px;
    border-radius: 5px;
    max-width: 70%;
    word-wrap: break-word;
    position: relative;
    display: flex;
    align-items: center;
}

.message-box.right {
    background-color: #d1e7dd;
    margin-left: auto;
    text-align: right;
}

.message-box.left {
    background-color: #f8d7da;
    margin-right: auto;
    text-align: left;
}

.sender-name {
    font-weight: bold;
    margin-right: 5px;
    display: flex;
    align-items: center;
}
